import styled from "styled-components";
import { Link } from "react-router-dom";
import React, { useState, useEffect } from "react";
import DatePicker from "react-datepicker";
import "react-datepicker/dist/react-datepicker.css";
import { ko } from "date-fns/esm/locale";

const Container = styled.div`
  width: 100vw;
  margin-top: 30px;
  margin-bottom: 10px;
  margin-left: 10vw;
  margin-right: 10vw;
  justify-content: center;
  text-align: center;
`;

const ContainerSearch = styled.div`
  justify-content: center;
  position: relative;
  height: fit-content;
`;

const BoxSearch = styled.div`
  height: fit-content;
  border: solid;
  border-width: 1px;
  border-color: rgba(0, 0, 0, 0.2);
  margin-top: auto;
  margin-bottom: auto;
  display: flex;
  font-size: 1.5vw;
`;

const BoxRegion = styled.div`
  margin-top: auto;
  margin-bottom: auto;
  font-size: 1.5vw;
  min-width: fit-content;
`;

const BoxCate = styled.div`
  margin-top: auto;
  margin-bottom: auto;
  font-size: 1.5vw;
  min-width: fit-content;
`;

const BoxFilter = styled.div`
  display: ${(props) => (props.FilterVisibility ? "inline-block" : "none")};
  font-size: 1.5vw;
`;

const BoxDate = styled.div`
  margin-top: 3vw;
  display: flex;
  font-size: 1.5vw;
  height: 4vw;
`;

const BtnWrite = styled.button`
  font-size: 1.8vw;
  width: 12vw;
  height: 4vw;
  border: none;
  background-color: orange;
  color: white;
  margin-left: 20px;
  border-radius: 5%;
  font-weight: bold;
`;

const InputSearch = styled.input`
  width: 25vw;
  height: 4vw;
  border: none;
  padding-inline: 10px;
  font-size: 1.5vw;
`;

const BtnSearch = styled.button`
  width: 7vw;
  height: 4vw;
  border: none;
  background-color: rgb(255, 224, 166);
  font-size: 1.5vw;
  min-width: fit-content;
`;

const InputPicker = styled.select`
  width: 12vw;
  height: 4vw;
  margin-left: 20px;
  text-align: left;
  border-color: rgba(0, 0, 0, 0.2);
`;

const TextDate = styled.div`
  font-size: 1vw;
  height: 3vw;
  line-height: 3vw;
  margin-right: 2vw;
  min-width: fit-content;
`;

const SDatePicker = styled(DatePicker)`
  border-radius: 4px;
  font-size: 1vw;
  border: 1px solid lightgray;
`;

function BoardOptionTab({ setOptions }) {
  const [inputs, setInputs] = useState({
    start: "",
    end: "",
    category: "",
    region: "",
    keyword: "",
    sort: 1,
  });

  const [startDate, setStartDate] = useState(false);
  const [endDate, setEndDate] = useState(false);

  const popUp = (event) => {
    if (sessionStorage.getItem("userID") === null) {
      alert("      .");
      event.preventDefault();
    }
  };

  const onSubmit = () => {
    setOptions(inputs);
  };

  return (
    <Container>
      <ContainerSearch>
        <div style={{ marginLeft: "5vw", marginRight: "5vw" }}>
          <div style={{ display: "flex" }}>
            {/*  */}
            <BoxSearch>
              <InputSearch
                placeholder=" "
                value={inputs.keyword}
                name="keyword"
                onChange={(e) =>
                  setInputs({
                    ...inputs,
                    keyword: e.target.value,
                  })
                }
              ></InputSearch>
              <BtnSearch onClick={onSubmit}></BtnSearch>
            </BoxSearch>
            {/*   */}
            <BoxRegion>
              <InputPicker
                onChange={(e) =>
                  setInputs({
                    ...inputs,
                    region: (e.target.value),
                  })
                }
              >
                <option key={0} value={""}>
                  
                </option>
                <option key={1} value={Number(1)}>
                  
                </option>
                <option key={2} value={Number(2)}>
                  
                </option>
                <option key={3} value={Number(3)}>
                  
                </option>
                <option key={4} value={Number(4)}>
                  
                </option>
                <option key={5} value={Number(5)}>
                  
                </option>
                <option key={6} value={Number(6)}>
                  
                </option>
                <option key={7} value={Number(7)}>
                  
                </option>
                <option key={8} value={Number(8)}>
                  
                </option>
                <option key={9} value={Number(9)}>
                  
                </option>
                <option key={10} value={Number(10)}>
                  
                </option>
                <option key={11} value={Number(11)}>
                  
                </option>
                <option key={12} value={Number(12)}>
                  
                </option>
                <option key={13} value={Number(13)}>
                  
                </option>
              </InputPicker>
            </BoxRegion>
            {/*   */}
            <BoxCate>
              <InputPicker
                onChange={(e) =>
                  setInputs({
                    ...inputs,
                    category: (e.target.value),
                  })
                }
              >
                <option key={0} value={""}>
                  
                </option>
                <option key={1} value={Number(1)}>
                  
                </option>
                <option key={2} value={Number(2)}>
                  
                </option>
                <option key={3} value={Number(3)}>
                  
                </option>
                <option key={4} value={Number(4)}>
                  
                </option>
              </InputPicker>
            </BoxCate>
            {/*  */}
            <BoxFilter>
              <InputPicker
                onChange={(e) =>
                  setInputs({
                    ...inputs,
                    sort: Number(e.target.value),
                  })
                }
              >
                <option key={1} value={1}>
                  
                </option>
                <option key={2} value={2}>
                  
                </option>
                <option key={3} value={3}>
                  
                </option>
              </InputPicker>
            </BoxFilter>
            {/* ,  */}
            <Link to="/board/edit">
              <BtnWrite onClick={popUp}></BtnWrite>
            </Link>
          </div>
          <BoxDate>
            <TextDate> :</TextDate>
            <div>
              <SDatePicker
                placeholderText="  "
                dateFormat="yyyy/MM/dd"
                selected={startDate}
                onChange={(date) => {
                  setStartDate(date);
                  var start =
                    date.getFullYear() +
                    ("0" + (date.getMonth() + 1)).slice(-2) +
                    ("0" + date.getDate()).slice(-2);
                  setInputs({ ...inputs, start: start });
                }}
                maxDate={new Date()}
                locale={ko}
              />
            </div>
            <div style={{ marginLeft: "2vw", marginRight: "2vw" }}>~</div>
            <div>
              <SDatePicker
                placeholderText="  "
                dateFormat="yyyy/MM/dd"
                selected={endDate}
                onChange={(date) => {
                  setEndDate(date);
                  var end =
                    date.getFullYear() +
                    ("0" + (date.getMonth() + 1)).slice(-2) +
                    ("0" + date.getDate()).slice(-2);
                  setInputs({ ...inputs, end: end });
                }}
                maxDate={new Date()}
                locale={ko}
              />
            </div>
          </BoxDate>
          <div style={{ fontSize: "1vw", color: "grey", textAlign: "left" }}>
            * //      , 
             .
          </div>
        </div>
      </ContainerSearch>
    </Container>
  );
}

export default BoardOptionTab;
